	module.exports = {"data":{"challengeNode":{"title":"Problem 408: Admissible paths through a grid","description":["Let's call a lattice point (x, y) inadmissible if x, y and x + y are all positive perfect squares.","For example, (9, 16) is inadmissible, while (0, 4), (3, 1) and (9, 4) are not.","","Consider a path from point (x1, y1) to point (x2, y2) using only unit steps north or east.","Let's call such a path admissible if none of its intermediate points are inadmissible.","","Let P(n) be the number of admissible paths from (0, 0) to (n, n).","It can be verified that P(5) = 252, P(16) = 596994440 and P(1000) mod 1 000 000 007 = 341920854.","","Find P(10 000 000) mod 1 000 000 007."],"challengeType":5,"fields":{"slug":"/coding-interview-prep/project-euler/problem-408-admissible-paths-through-a-grid","blockName":"Project Euler","tests":[{"text":"<code>euler408()</code> should return 299742733.","testString":"assert.strictEqual(euler408(), 299742733, '<code>euler408()</code> should return 299742733.');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"function euler408() {\n  // Good luck!\n  return true;\n}\n\neuler408();","head":"","tail":""},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/coding-interview-prep/project-euler/problem-409-nim-extreme","id":"5900f5091000cf542c51001b"},"slug":"/coding-interview-prep/project-euler/problem-408-admissible-paths-through-a-grid"}}

